@use 'base/variables' as v;
@use 'base/mixins' as m;
.header {
    padding: 5rem 0 ;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    @include m.tablet { 
        min-height: 70rem;
    }

    
    
    &::before {
        content: '';
        background-color: v.$negro;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: .6;
    }
    .contenido-header {
        position: relative;

        h1 {
            color: v.$blanco;
            text-align: center;
            text-transform: uppercase;
            margin-top: 5rem;
            @include m.tablet { 
                margin-top: 10rem;
            }
        }
    }
  
}
// header de cada pagina
.header-inicio {
    background-image: url(/build/img/header_inicio.jpg);
}
