@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos {
    margin-top: 5rem;
    .listado-iconos{
        @include m.tablet { 
            // @include m.grid;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            column-gap: 5rem;
        }
        li {
            img {
                margin: 0 auto;
                width: 12rem;
            }
            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}