html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: black;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: black;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5E311E;
}
h2 span, .heading-blanco span {
  color: #C39C63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .heading-blanco::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(/build/img/cafe.svg);
  background-size: 10rem;
}

.heading-blanco {
  color: white;
}
.heading-blanco span {
  color: white;
}
.heading-blanco::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .contenido-header {
  position: relative;
}
.header .contenido-header h1 {
  color: white;
  text-align: center;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header h1 {
    margin-top: 10rem;
  }
}

.header-inicio {
  background-image: url(/build/img/header_inicio.jpg);
}

.header-nosotros {
  background-image: url(/build/img/header_nosotros.jpg);
}

.header-proceso {
  background-image: url(/build/img/header_proceso.jpg);
}

.header-menu {
  background-image: url(/build/img/header_menu.jpg);
}

.header-galeria {
  background-image: url(/build/img/header_galeria.jpg);
}

.header-contacto {
  background-image: url(/build/img/header_contacto.jpg);
}

.nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 1rem;
    margin-top: 0;
  }
}
.nav-principal a {
  text-align: center;
  color: white;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #C39C63;
  transition: all 300ms;
}
.nav-principal a.activo {
  color: #C39C63;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
    margin: 0;
  }
}

.footer {
  background-color: #5E311E;
}
.footer .contenido-footer {
  padding: 1rem 5rem 5rem 5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 5rem 0 1rem 0;
  font-size: 2.4rem;
  color: #C39C63;
}
.footer-box p, .footer-box a {
  font-size: 2rem;
  margin: 0;
}

.copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: white;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contenido-nosotros .imagen {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contenido-nosotros .imagen {
    margin: 0 2rem 0 0;
  }
}
.contenido-nosotros .imagen img {
  transform: rotate(-6deg);
  border: 3rem solid white;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}
.contenido-nosotros .texto blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5E311E;
  font-size: 2rem;
}

.proceso-cafe {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .proceso-cafe {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.proceso-cafe:last-of-type {
  margin-bottom: 0;
}
.proceso-cafe:nth-child(even) .texto {
  order: -1;
}
.proceso-cafe .imagen,
.proceso-cafe .texto {
  flex: 1;
}
.proceso-cafe .texto {
  margin-top: 1rem;
}
.proceso-cafe .texto h3 {
  margin: 0;
  text-align: left;
  color: #5E311E;
}
.proceso-cafe .texto h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 5px;
  background-color: #C39C63;
}

.contenido-menu {
  background-image: url(/build/img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/5;
}
@media (min-width: 400px) {
  .contenido-menu {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .contenido-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contenido-menu .texto p {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .contenido-contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(/build/img/granos_cafe.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.formulario .campo {
  margin-bottom: 2rem;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.formulario .campo input:not([type=submit]),
.formulario .campo select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5E311E;
}
.formulario .campo input[type=submit] {
  background-color: #5E311E;
  width: 100%;
  border: none;
  padding: 1rem;
  color: white;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.formulario .campo input[type=submit]:hover {
  cursor: pointer;
  opacity: 0.8;
}

.contenido-principal {
  background-color: white;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

.iconos {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .iconos .listado-iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}
.iconos .listado-iconos li img {
  margin: 0 auto;
  width: 12rem;
}
.iconos .listado-iconos li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5E311E;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.galeria .imagen img {
  height: 30rem;
  width: 30rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .galeria .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .galeria .imagen:nth-child(1) img {
    height: 62rem;
    object-position: center;
  }
  .galeria .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .galeria .imagen:nth-child(2) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .galeria .imagen:nth-child(4) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(5) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(6) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .galeria .imagen:nth-child(7) img {
    height: 62rem;
  }
  .galeria .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .galeria .imagen:nth-child(8) img {
    width: 94rem;
  }
  .galeria .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .galeria .imagen:nth-child(9) img {
    width: 62rem;
  }
  .galeria .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .galeria .imagen:nth-child(10) img {
    width: 62rem;
  }
}

.nuestro-menu {
  background-image: url(/build/img/menu_arriba.svg), url(/build/img/menu_abajo.svg), linear-gradient(to top, #5E311E, #5E311E);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem 2rem;
}
@media (min-width: 768px) {
  .nuestro-menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .nuestro-menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: white;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu li p {
  color: white;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem 0;
}
.menu li p span {
  display: block;
  color: #C39C63;
}
.menu li .precio {
  font-size: 4rem;
  font-weight: 700;
}

.nuestros-testimoniales {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .nuestros-testimoniales {
    padding: 10rem;
  }
}
.nuestros-testimoniales .testimonial {
  background-color: #ebe7de;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
}
.nuestros-testimoniales .testimonial .autor {
  text-align: right;
  font-size: 2.4rem;
  color: #5E311E;
  font-family: "Bona Nova", serif;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5E311E;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: white;
}

.padding-bottom-0 {
  padding-bottom: 0;
}